body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.avatar {
  border-radius: 50%;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.welcome-message {
  margin-top: 60px;
  padding: 25px;
  background-color: #282c34;
  text-align: center;
}
.welcome-message h1 {
  color: white;
}
.welcome-message p {
  color: whitesmoke;
}

.featured {
  background-color: greenyellow;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.featured h2 {
  font-size: 24;
  margin-bottom: 10px;
}

.featured p {
  font-size: 16px;
  margin-bottom: 8px;
}

.featured button {
  padding: 10px 20px;
  background-color: blue;
  color:white;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  cursor: pointer;
}

.puzzle-showcase {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  border: 2px solid black;
  padding: 10px;
}
.puzzle-image {
  margin-right: 20px;
}
.puzzle-image img {
  max-width: 350px;
  height: auto;
}

.puzzle-description {
  flex: 1 1;
  margin-right: 20px;
}

.puzzle-description h3 {
  font-size: 24px;
  margin-bottom: 10px;
}

.puzzle-description p {
  font-size: 16px;
  margin-bottom: 8px;
}

/* Media Queries for Responsive Design */
@media (max-width: 768px) {
  .welcome-message {
    margin-top: 50px;
    padding: 70px;
  }

  .featured {
    padding: 70px;
  }

  .featured h2 {
    font-size: 24px;
    margin-bottom: 8px;
  }

  .featured p {
    font-size: 15px;
    margin-bottom: 8px;
  }

  .featured button {
    padding: 8px 16px;
    font-size: 16px;
  }

  .puzzle-showcase {
    flex-direction: column;
    margin-bottom: 15px;
  }

  .puzzle-image {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .puzzle-image img {
    max-width: 300px;
    height: auto;
  }

  .puzzle-description {
    margin-right: 0;
  }

  .puzzle-description h3 {
    font-size: 20px;
    margin-bottom: 8px;
  }

  .puzzle-description p {
    font-size: 14px;
    margin-bottom: 6px;
  }
}
.header {
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    background-color: white;
    color: black;
    padding: 10px;
    z-index: 100;
  }
  
.logo {
    margin-top: 0px;
    height: 7vmin;
    pointer-events: none;
  }
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.nav ul {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
}

.nav li {
    margin-right: 25px;
  }
  
.nav a {
    text-decoration: none;
    color: black;
  }

.nav a:hover {
    text-decoration: underline;
  }

.nav-text    {
    margin-top: 25px;
}

@media (max-width: 768px) {
  .logo {

    height: 12vmin;
    float: left
  }
  .menu {
    height: 12vmin;
  }
  .dropdown {
    position: relative;
    float: right;
    padding-right: 20px;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    top: calc(100% + 10.5px);
    right: 0;
    background-color: white;
    padding: 10px;
    text-align: center;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    z-index: 1;
  }

  .dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    margin-bottom: 10px;
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }

}
